//this scss file contains the necessary style for menu style

/*****************************
	--- MENU
*****************************/
.pre-header-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: .6rem;
  background-color: $primary;
  .header-left {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    span {
      color: white;
      font-size: remy(13px);
    }
  }
  .header-right {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 2rem;
    .dropdown {
      margin-left: 1.5rem;
      font-size: .8rem;
      .dropdown-toggle {
        color: $white;
        &:after{
          content: '\f107';
          font-family: "Line Awesome Free";
          font-weight: 900;
          font-size: remy(10px);
          padding-left: 3px;
        }
      }
      .dropdown-menu {
        top: 9px !important;
        border: none;
      }
    }
  }
  .modal-header {
    background-color: $primary;
    color: white;
    justify-content: center;
    font-weight: 500;
  }
  .country-list {
    .country-item {
      display: inline-block;
      width: 100%;
      color: black;
      padding: 0 1rem;
      margin: .5rem 0;
      &:hover {
        background-color: #f8f9fa;
      }
    }
  }
}

/*****************************
	--- MENU
*****************************/
.mainmenu-wrapper{
  position: relative;
  z-index: 3;
  &.sticky-menu {
    position: fixed;
    top: 0;
    width: 100%;
    background: white;
  }
}

.menu-area{
  .top-menu-area{
    box-shadow: 0 2px 6px 0 hsla(0,0%,58.4%,.5);
    .logo-top{
      text-align: center;
      a{
        display: block;
      }
    }
  }
  .mainmenu__menu .navbar-nav > li{
    &:hover{
      >a{
        @extend .color-primary;
      }
    }
    &.has_dropdown{
      position: relative;
      .dropdown-toggle{
        &:after{
          font-family: 'Line Awesome Free';
          font-weight: 900;
          content: "\f107";
          font-size: remy(10px);
          padding-left: 3px;
        }
      }
      &:hover{
        .dropdown-menu{
          visibility: visible;
          opacity: 1;
        }
      }
      .dropdown-menu{
        display: block;
        padding:remy(15px) 0;
        min-width: remy(200px);
        border-radius: 0 0 3px 3px;
        visibility: hidden;
        opacity: 0;
        transition: $transition-base;
        z-index: 10;
        border: 0 none;
        box-shadow: 0 5px 15px rgba($dark, 0.1);
        top: 98%;
        a{
          display: block;
          padding: remy(8px) remy(25px);
        }
        li:hover a{
          @extend .color-primary;
        }
        .dropdown-menu--inner{
          position: relative;
          & > a{
            &:after{
              content: '\f107';
              font-family: "Line Awesome Free";
              font-weight: 900;
              font-size: remy(10px);
              padding-left: 0;
              right: remy(15px);
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
            }
          }
          & > ul{
            position: absolute;
            left: 100%;
            top: 0;
            width: remy(160px);
            visibility: hidden;
            opacity: 0;
            transition: $transition-base;
            padding:remy(7px) 0;
            box-shadow: 0 5px 15px rgba($dark, 0.1);
            li{
              a{
                display: block;
                background: none;
                &:hover{
                  @extend .color-primary;
                }
              }
            }
          }
          &:hover{
            & > ul{
              visibility: visible;
              opacity: 1;
            }
          }
        }
      }
    }
  }
  .author__notification_area ul li .notification_count.purch{
    @extend .bg-secondary;
  }
  .author__notification_area{
    ul li{
      padding: 30px 0;
    }
  }
  .navbar-toggler{
    border: 1px solid;
    padding: 3px;
    border-radius: 2px;
    outline: 0;
    .icon-menu{
      font-size: remy(20px);
      display: inline-block;
      line-height: normal;
      margin-bottom: -3px;
    }
  }
  &.menu--light{
    .top-menu-area{
      background: transparent;
      // border-bottom: 1px solid rgba(#fff, 0.1);
    }
    nav.mainmenu__menu {
      position: unset;
    }
    .mainmenu__menu .navbar-nav > li{
      border-bottom: 4px solid transparent;
      &:hover {
      border-bottom: 4px solid $primary;
        > a{
          color: $primary;
        }
      }
      > a{
        color: $dark-blue;
      }
      &.has_dropdown{
        .dropdown-menu{
          a{
            color: $text-grey1;
          }
          .dropdown-menu--inner{
            & > ul{
              background: #fff;
              li{
                a{
                  color: $text-grey1;
                  &:hover{
                    @extend .color-primary;
                  }
                }
              }
            }
          }
        }
      }
    }
    .search-form button,
    .author__notification_area ul li span:first-child{
      color: rgba(255,255,255,0.8);
    }
    .author__notification_area{
      border-color: rgba(#fff, 0.1);
    }
    .search-wrapper{
      .search_module{
        .icon-left{
          color: $dark;
        }
        .search_area{
          form{
            .input-group{
              .form-control{
                border-color: rgba($dark, 0.1);
                background: #fff;
                color: $dark;
                &::placeholder{
                  color: $light-gray;
                }
              }
            }
          }
        }
      }
    }
    .author__access_area ul li .access-link{
      font-weight: bold;
      color: rgba($primary, 0.8);
    }
    .author__access_area ul li > span{
      color: rgba(#000, 0.5);
    }
    .navbar-toggler{
      font-weight: bold;
      border-color: rgba(#fff, 0.7);
    }
  }
  &.menu--dark{
    .top-menu-area{
      background: transparent;
      border-bottom: 0 none;
      box-shadow: 0 5px remy(15px) rgba($dark, 0.05);
      .logo-wrapper{
        border-color: rgba($dark, 0.15);
      }
    }
    .mainmenu__menu .navbar-nav > li{
      > a{
        color: $dark;
      }
      &.has_dropdown{
        .dropdown-menu{
          a{
            color: $text-grey1;
          }
          .dropdown-menu--inner{
            & > ul{
              background: #fff;
              li{
                a{
                  color: $text-grey1;
                }
              }
            }
          }
        }
      }
    }
    .search-form button,
    .author__notification_area ul li span:first-child{
      color: rgba(255,255,255,0.8);
    }
    .author__notification_area{
      border-color: rgba(#fff, 0.1);
    }

    .search-wrapper{
      .search_module{
        .icon-left{
          color: $dark;
        }
        .search_area{
          form{
            .input-group{
              .form-control{
                color: $dark;
                &::placeholder{
                  color: rgba($dark, 0.5);
                }
              }
            }
          }
        }
        &.active{
          .icon-left{
            color: $dark;
          }
          .search_area{
            form .input-group .form-control{
              border-color: rgba($dark, 0.1);
              background: #fff;
              color: $dark;
              &::placeholder{
                color: $light-gray;
              }
            }
          }
        }
      }
    }
    .author__access_area ul li .access-link{
      color: $dark;
    }
    .author__access_area ul li > span{
      color: rgba($dark, 0.5);
    }
    .navbar-toggler{
      border-color: rgba($dark, 0.7);
      .icon-menu{
        color: rgba($dark, 0.7);
      }
    }
  }
}


/*****************************
	START MENU AREA
*****************************/
.top-menu-area {
  background: #fff;
  // border-bottom: 1px solid $border-color;
  .menu-fullwidth {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    .menu-right {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
  }
}
.search-wrapper{
  // margin-right: 25%;
  position: relative;
  min-width: 30rem;
  .search_module{
    position: relative;
    .icon-left{
      position: absolute;
      left: remy(10px);
      top: 50%;
      transform: translateY(-50%);
      font-size: remy(18px);
      transition: $transition-base;
      z-index: 10;
    }
    .search_area{
      form{
        .input-group{
          margin: 0;
          .form-control{
            background: none;
            padding-left: remy(40px);
            border: 1px solid transparent;
            transition: $transition-base;
            width: 100%;
            &::placeholder{
              transition: $transition-base;
            }
            &:focus{
              outline: 0;
              box-shadow: none;
            }
          }
        }
      }
    }
    &.active{
      & + .search-categories{
        visibility: visible;
        opacity: 1;
      }
      .search_area{
        form .input-group .form-control{
          border: 1px solid;
          border-radius: 3px 3px 0 0;
        }
      }
    }
  }
  .search-categories{
    border: 1px solid rgba(#fff, 0.2);
    border-radius: 0 0 3px 3px;
    position: absolute;
    width: 100%;
    left: 0;
    top: remy(48px);
    z-index: 100;
    box-shadow: 0 5px remy(10px) rgba($dark, 0.1);
    visibility: hidden;
    opacity: 0;
    transition: $transition-base;
    background: #fff;
    ul{
      margin: 0;
      li{
        a{
          color: $text-grey1;
          padding: remy(10px) remy(15px);
          display: block;
          span{
            width: remy(34px);
            height: remy(34px);
            border-radius: remy(300px);
            display: inline-flex;
            align-items: center;
            justify-content: center;
            margin-right: remy(8px);
            color: #fff;
          }
        }
        &:hover{
          background: #f5f7fc;
        }
      }
    }
  }
}

.top-menu-area .d_menu {
  padding-left: 35px;
}

.top-menu-area .top__menu ul li {
  display: inline-block;
  a{
    color: #555;
  }
}

.author-area {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}

.author__access_area {
  padding: 14px 20px 14px 35px;
  ul{
    margin:0;
    li {
      margin-right: remy(30px);
      &:last-child {
        margin-right: 0;
      }
      .access-link{
        color: rgba($dark, 0.05);
        font-size: remy(15px);
        margin: 0 5px;
        &:hover{
          @extend .color-primary;
        }
      }
      & > span{
        display: inline-block;
        padding: 0 4px;
      }
      .author-info{
        position: relative;
        &:after{
          position: absolute;
          content: '';
          height: 20px;
          width: 100%;
          background: transparent;
          left: 0;
          bottom: -20px;
        }
        .author-avatar{
          display: block;
        }
        &:hover{
          ul{
            visibility: visible;
            opacity: 1;
          }
        }
        ul{
          background: #fff;
          box-shadow: 0 -3px remy(30px) rgba($dark, 0.1);
          position: absolute;
          right: 0;
          min-width: remy(200px);
          top: remy(70px);
          padding:remy(15px) 0;
          visibility: hidden;
          opacity: 0;
          z-index: 4;
          transition: $transition-base;
          &:before{
            position: absolute;
            content: '';
            border-bottom: 13px solid #fff;
            border-left: 8px solid transparent;
            border-right: 8px solid transparent;
            top: -13px;
            right: remy(15px);
          }
          li{
            margin: 0;
            padding: 0 remy(10px);
            a{
              display: block;
              color: $text-grey1;
              padding: remy(7px) remy(15px);
              border-radius: 3px;
              &:hover{
                color: #fff;
                @extend .bg-secondary;
              }
            }
          }
        }
      }
    }
  }
}


/* start mainenu area css */
.mainmenu__menu {
  border: none;
  margin: 0;
  padding: 0;
  .navbar-collapse {
    padding: 0;
    float: left;
  }
}

.mainmenu__menu .navbar-nav > li {
  display: inline-block;
  padding: 10px 0;
  margin-right: 30px;
  &:hover {
    > a {
      @extend .color-primary;
    }
  }
  > a {
    font-weight: 500;
    padding: 0;
    font-size: remy(14px);
    line-height: 42px;
    transition: $transition-base;
    text-transform: capitalize;
  }
  &.drop-mega {
    &:hover {
      .mega-menu {
        display: flex;
      }
    }
    .mega-menu {
      box-shadow: 0 2px 6px 0 hsla(0,0%,58.4%,.5);
      top: 65px;
      background: white;
      left: 0;
      margin-top: 3px;
      position: absolute;
      width: 100%;
      display:none;
      transition-duration: 0.9s;
      .side-menu {
        width: 300px;
        background: $gray-200;
        padding: 20px 0 20px 20px;
        ul {
          list-style: none;
          padding-left: 0;
          li {
            padding: 10px;
            font-weight: bolder;
            cursor: pointer;
            &:before {
              display: none;
            }
            &.active {
              background: white;
            }
            &:hover {
              background: white;
            }
          }
        }
      }
      .sub-menu-list {
        padding: 20px;
        .category-list {
          list-style: none;
          span {
            display: block;
            color: $primary;
            font-weight: bolder;
            font-size: remy(15px);
            padding-bottom: 0.5rem;
          }
          li {
            font-size: remy(12px);
            padding: 12px 0;
            &:before {
              display: none;
            }
            a {
              color: #3c3c3c;
              font-size: remy(15px);
              transition: all ease 0.5s;
              &.text-warning {
                color: $warning;
                font-weight: bold;
                &:hover {
                  color: $warning;
                  border-bottom: 1px solid $warning;
                }
              }
              &:hover {
                color: $primary;
                border-bottom: 1px solid $primary;
              }
            }
          }
        }
      }
    }
  }
}

/* offcanvas menu */
.offcanvas-menu{
  display: flex;
  align-items: center;
  margin-left: 20px;
  position: relative;
  .offcanvas-menu__user{
    // @extend .bg-primary;
    background: white;
    z-index: 3;
    color: black;
    display: flex;
    font-size: remy(20px);
    margin-right: remy(30px);
    border-radius: 50px;
    padding: 3px 10px;
    align-items: center;
    border: 1px solid rgba($light-gray, .5);
    .unread-msg {
      position: absolute;
      background-color: #ed0103;
      top: -7px;
      right: 63px;
      z-index: 1;
      border-radius: 50%;
      border: 3px solid white;
      padding: 0;
      width: 30px;
      height: 30px;
      font-size: remy(13px);
      font-weight: bolder;
      color: white;
    }
    i {
      margin: 0 10px 0 5px;
      &.la-user {
        padding: remy(4px) 0;
        font-size: remy(30px);
        line-height: 30px;
        margin: 0;
      }
    }
    img {
      width: 40px;
      height: 40px;
      object-fit: cover;
    }
  }
  .offcanvas-menu__cart{
    display: flex;
    align-items: center;
    background: $primary;
    z-index: 2;
    border-radius: 50px;
    margin-left: -70px;
    padding: 13.5px 22px 13.5px 50px;
    color: white;
    font-weight: bolder;
    img {
      width: 20px;
      margin-right: 5px;
    }
  }
  .offcanvas-menu__contents{
    &.active{
      right: -10px;
      transition: $transition-base;
    }
    position: fixed;
    height: 100%;
    width: 220px;
    top: 0;
    right: -250px;
    background: #fff;
    z-index: 989999;
    overflow-y: scroll;
    transition: $transition-base;
    box-shadow: 0 -2px 20px rgba($dark, 0.08);
    padding: remy(25px);
    .offcanvas-menu__close{
      position: absolute;
      left: remy(15px);
      top: remy(15px);
      font-size: remy(22px);
      color: $text-grey2;
    }
    .author-avatar{
      text-align: center;
      padding: remy(15px) 0 0;
      img {
        width: 80%;
        height: auto;
        object-fit: cover;
        margin-bottom: 1rem;
      }
      
    }
    ul{
      li{
        &.group-title {
          background: $gray-500;
          text-align: center;
          color: white;
          padding: .3333rem 0;
          margin: 1rem -25px;
        }
        &.log-out {
          border: 1px solid $primary;
          border-radius: 5px;
          margin-top: 2rem;
          text-align: center;
          a {
            font-weight: bolder;
            color: $primary;
          }
        }
        a{
          display: block;
          padding: 8px remy(10px);
          border-radius: 3px;
          color: $text-grey1;
          transition: none !important;
          &:hover{
            @extend .bg-primary;
            color: #fff;
          }
        }
      }
    }
    .search_area{
      margin-top: remy(20px);
      text-align: center;
      .btn{
        margin-top: remy(10px);
      }
    }
  }
}

.country-dropdown {
  max-height: 400px;
  overflow-y: auto;
}


/*****************************
	END MENU AREA CSS
*****************************/
